import React , { useState } from 'react';
import './Content.css'

import Home from '../../components/Home/Home'

const Content = props => {
    const [ view , setView ] = useState(1)

    const changeView = () => {
        if(view===1){
            setView(2)
        }else{
            setView(1)
        }
    }

    return (
            <div className="Content">
                <Home changeView = {changeView} view={view}/>
            </div>

    )
}


export default Content